.scrolltop {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center; }

.scrolltop {
  z-index: 4;
  position: fixed;
  right: 3vw;
  bottom: 7vh;
  width: 40px;
  height: 40px;
  font-weight: bold;
  color: #ffd99f;
  background-color: #ec5c1e;
  border: 2px solid #ffd99f;
  border-radius: 50%;
  opacity: 0.7;
  transition-property: color, background-color, opacity, transform, visibility;
  transition-duration: 0.4s;
  cursor: pointer;
  user-select: none;
  visibility: visible; }
  .scrolltop:hover, .scrolltop:active {
    color: #ec5c1e;
    border: 2px solid #ec5c1e;
    background-color: #ffd99f;
    opacity: 1;
    transform: scale(1.1, 1.1); }
    .scrolltop:hover path, .scrolltop:active path {
      color: #ec5c1e; }
  .scrolltop.hide {
    opacity: 0;
    visibility: hidden; }
  .scrolltop svg {
    width: 60%;
    height: 60%; }
    .scrolltop svg path {
      color: #ffd99f; }
