.nav ul, .nav ul li {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center; }

.nav ul li {
  flex: 1;
  font-weight: bold;
  cursor: pointer; }
  .nav ul li a {
    padding: 10px;
    display: block;
    white-space: nowrap;
    text-decoration: none;
    color: inherit;
    transition: background-color 0.3s; }
  .nav ul li:hover::after, .nav ul li:active::after {
    content: "";
    display: block;
    position: absolute;
    width: 0;
    height: 0;
    border: 6px solid transparent;
    border-bottom: 6px solid #ffd99f;
    margin-bottom: -2px;
    bottom: 0.2rem; }
  .nav ul li.active::before {
    content: "";
    display: block;
    position: absolute;
    width: 0;
    height: 0;
    border: 6px solid transparent;
    border-bottom: 6px solid #ffd99f;
    margin-bottom: -2px;
    bottom: 0.2rem; }
