.contact, .contact > * {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center; }

.contact > * {
  margin-left: 0.5rem;
  color: #ffd99f;
  background: #ec5c1e;
  border-radius: 2rem;
  cursor: pointer;
  transition-property: color, background-color;
  transition-duration: 0.3s; }
  @media only screen and (min-width: 1248px) {
    .contact > * {
      width: 25px; } }
  @media only screen and (max-width: 1247px) {
    .contact > * {
      width: 25px; } }
  @media only screen and (max-width: 767px) {
    .contact > * {
      width: 25px; } }
  @media only screen and (max-width: 559px) {
    .contact > * {
      width: 25px; } }
  @media only screen and (max-width: 399px) {
    .contact > * {
      width: 20px; } }
  @media only screen and (min-width: 1248px) {
    .contact > * {
      height: 25px; } }
  @media only screen and (max-width: 1247px) {
    .contact > * {
      height: 25px; } }
  @media only screen and (max-width: 767px) {
    .contact > * {
      height: 25px; } }
  @media only screen and (max-width: 559px) {
    .contact > * {
      height: 25px; } }
  @media only screen and (max-width: 399px) {
    .contact > * {
      height: 20px; } }
  .contact > *:first-of-type {
    margin-left: 0; }
  .contact > *:hover, .contact > *:active {
    color: #ec5c1e;
    background: #ffd99f; }
    .contact > *:hover path, .contact > *:active path {
      fill: #ec5c1e; }
  .contact > * svg {
    width: 80%;
    height: 80%; }
    .contact > * svg path {
      fill: #ffd99f; }
