html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* remember to define focus styles! */
:focus {
  outline: 0; }

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }
  blockquote:before, blockquote:after,
  q:before,
  q:after {
    content: '';
    content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

a:link, a:visited, a:hover, a:active {
  color: inherit; }

* {
  box-sizing: border-box;
  border-color: transparent;
  outline: none; }
  *::before, *::after {
    box-sizing: inherit; }

body {
  font-family: "Noto Sans TC", "Segoe UI", "微軟正黑體", Arial, sans-serif;
  color: #ec5c1e;
  background: #ffd99f;
  overflow-x: hidden; }
  body * {
    font-size: 12px; }
  @media (min-width: 320px) and (max-width: 1024px) {
    body * {
      font-size: calc( 12px + 12 * (100vw - 320px) / 704); } }
  @media (min-width: 1024px) {
    body * {
      font-size: 24px; } }
  @media only screen and (min-width: 1248px) {
    body * {
      line-height: 1.5em; } }
  @media only screen and (max-width: 1247px) {
    body * {
      line-height: 1.5em; } }
  @media only screen and (max-width: 767px) {
    body * {
      line-height: 1.5em; } }
  @media only screen and (max-width: 559px) {
    body * {
      line-height: 1.25em; } }
  @media only screen and (max-width: 399px) {
    body * {
      line-height: 1.25em; } }

