.sticky {
  position: fixed;
  top: 0;
  width: 100%; }

/* Make clicks pass-through */
#nprogress {
  pointer-events: none; }
  #nprogress .bar {
    z-index: 5;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background: #ffd99f; }
  #nprogress .peg {
    position: absolute;
    right: 0;
    display: block;
    width: 100px;
    height: 100%;
    box-shadow: 0 0 10px #ffd99f, 0 0 5px #ffd99f;
    opacity: 1;
    transform: rotate(3deg) translate(0px, -4px); }
  #nprogress .spinner {
    z-index: 5;
    position: fixed;
    top: 15px;
    right: 15px;
    display: block; }
  #nprogress .spinner-icon {
    width: 18px;
    height: 18px;
    border: solid 2px transparent;
    border-top-color: #ffd99f;
    border-left-color: #ffd99f;
    border-radius: 50%;
    animation: nprogress-spinner 400ms linear infinite; }

/* Fancy blur effect */
/* Remove these to get rid of the spinner */
.nprogress-custom-parent {
  position: relative;
  overflow: hidden; }
  .nprogress-custom-parent #nprogress .spinner,
  .nprogress-custom-parent #nprogress .bar {
    position: absolute; }

@keyframes nprogress-spinner {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(360deg); } }
