.box .mask .category {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center; }

.more,
.loading {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }

.box {
  position: relative;
  margin: 0.5rem;
  display: block;
  line-height: 0;
  font-size: 0;
  box-shadow: 4px 4px 6px rgba(236, 92, 30, 0.3);
  overflow: hidden; }
  .box:hover {
    box-shadow: 4px 4px 12px rgba(236, 92, 30, 0.3); }
    .box:hover .mask {
      visibility: visible;
      top: 0; }
  .box img {
    max-width: 100%;
    height: auto;
    vertical-align: top;
    border-style: none; }
  .box .mask {
    position: absolute;
    top: -100%;
    left: 0;
    width: 100%;
    height: 100%;
    visibility: hidden;
    transition-property: top, visibility;
    transition-duration: 0.3s; }
    .box .mask .category {
      margin: 0;
      padding: 0;
      justify-content: start;
      line-height: 0; }
      .box .mask .category span {
        margin: 1px;
        padding: 1px;
        color: #222222;
        background-color: rgba(255, 255, 255, 0.5);
        border-radius: 0.2em; }
        .box .mask .category span {
          font-size: 12px; }
        @media (min-width: 320px) and (max-width: 1024px) {
          .box .mask .category span {
            font-size: calc( 12px + 0 * (100vw - 320px) / 704); } }
        @media (min-width: 1024px) {
          .box .mask .category span {
            font-size: 12px; } }
    .box .mask figcaption {
      margin: 0.2em 0 0;
      display: block;
      font-weight: normal; }

.tips {
  z-index: 4;
  margin-top: 30px;
  width: 100%;
  text-align: center; }

.more,
.loading {
  z-index: 4;
  top: 85vh;
  display: block; }

.loading {
  width: 18px;
  height: 18px;
  border: solid 2px transparent;
  border-top-color: #e23939;
  border-left-color: #e23939;
  border-radius: 50%;
  animation: nprogress-spinner 0.5s linear infinite; }

@keyframes nprogress-spinner {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(360deg); } }
