.app, .header,
.footer, .header .main {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center; }

.fixBG {
  z-index: -100;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background: #ffd99f; }

.app {
  flex-direction: column;
  min-height: 100vh; }

.wrapper {
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  max-width: 1248px; }

.header,
.footer {
  width: 100%; }
  .header .wrapper,
  .footer .wrapper {
    padding: 0.2em;
    display: flex;
    align-items: center; }

.header {
  z-index: 3;
  position: fixed;
  top: 0;
  color: #ffd99f;
  background: #ec5c1e;
  opacity: 0.8;
  transform: translateY(0);
  transition-property: opacity, transform;
  transition-duration: 0.3s;
  transition-timing-function: cubic-bezier(0.47, 1.04, 1, 1); }
  @media only screen and (min-width: 1248px) {
    .header {
      height: 60px; } }
  @media only screen and (max-width: 1247px) {
    .header {
      height: 60px; } }
  @media only screen and (max-width: 767px) {
    .header {
      height: 55px; } }
  @media only screen and (max-width: 559px) {
    .header {
      height: 50px; } }
  @media only screen and (max-width: 399px) {
    .header {
      height: 45px; } }
  @media only screen and (min-width: 1248px) {
    .header + .content {
      margin-top: 60px; } }
  @media only screen and (max-width: 1247px) {
    .header + .content {
      margin-top: 60px; } }
  @media only screen and (max-width: 767px) {
    .header + .content {
      margin-top: 55px; } }
  @media only screen and (max-width: 559px) {
    .header + .content {
      margin-top: 50px; } }
  @media only screen and (max-width: 399px) {
    .header + .content {
      margin-top: 45px; } }
  .header:hover, .header:active, .header.top {
    opacity: 1; }
  .header.hide {
    transform: translateY(-100%); }
  .header .wrapper {
    justify-content: space-between; }
  .header .main .logo {
    position: relative;
    padding-top: 100%;
    width: 40px;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    background-image: url(../../../static/images/logo.png);
    cursor: pointer;
    transition: opacity 0.5s; }
    .header .main .logo:hover, .header .main .logo:active {
      opacity: 0.8; }

.content {
  flex: 1;
  width: 100%; }
  @media only screen and (min-width: 1248px) {
    .content {
      padding-bottom: 35px; } }
  @media only screen and (max-width: 1247px) {
    .content {
      padding-bottom: 35px; } }
  @media only screen and (max-width: 767px) {
    .content {
      padding-bottom: 30px; } }
  @media only screen and (max-width: 559px) {
    .content {
      padding-bottom: 25px; } }
  @media only screen and (max-width: 399px) {
    .content {
      padding-bottom: 20px; } }

.footer {
  z-index: 2;
  position: fixed;
  left: 0;
  bottom: 0;
  color: #ffd99f;
  background: #ec5c1e; }
  .footer .wrapper {
    justify-content: center;
    flex-direction: column; }
  .footer b {
    font-weight: bold; }
    .footer b {
      font-size: 12px; }
    @media (min-width: 320px) and (max-width: 1024px) {
      .footer b {
        font-size: calc( 12px + 4 * (100vw - 320px) / 704); } }
    @media (min-width: 1024px) {
      .footer b {
        font-size: 16px; } }
